import { afterHooks,beforeHooks,registerRouter} from './concat.js'
import {fromatRoutes} from './util.js'
import {err,warn} from '../helpers/warn.js'
import {proxyEachHooks} from './proxy/proxy.js'
/**
 * H5 uni-appbug
 * 
 * @param {Object} Router
 */
const rewriteUniFun = function(Router) {
	if(Router.CONFIG.h5.rewriteFun===false){	//
		return false
	}
	uni.reLaunch = function({
		url
	}) {
		if(url==='/'){
			 warn(`H5 uni.reLaunch('/') !  this.$Router.replaceAll()  uni.reLaunch('/'?xxx)`);
			 if(history.length>1){		//  
				 return Router.back();
			 }
			 return Router.replaceAll('/');
		}
		const path=url.match(/^[^?]+|(\/)/)[0];
		try{
			const query = {};
			url.replace(/([^?&]+)=([^?&]+)/g, function(s, v, k) {
				query[v] = decodeURIComponent(k);
				return k + '=' + v;
			});
			Router.replaceAll({
				path,
				query
			})
		}catch(e){
			err(`${url}....   this.$Router.replaceAll() `);
		}
	}
	uni.navigateBack = function(delta) {
		let backLayer=delta;
		if (delta.constructor === Object) { //uni-app,
			backLayer = 1;
		}
		Router.back(backLayer,delta)
	}
}
/**
 * vueRouter
 * @param {Object} Router 
 * @param {vueRouter} vueRouter 
 * @param {VueComponent} vueVim
 */
export const init = function(Router, vueRouter, vueVim) {
	const CONFIG = Router.CONFIG.h5;
	vueRouter.afterHooks = proxyEachHooks(Router, 'afterHooks', afterHooks);
	vueRouter.beforeHooks = proxyEachHooks(Router, 'beforeHooks', beforeHooks);
	const objVueRoutes = fromatRoutes(vueRouter.options.routes, false, {}); //routes 
	const objSelfRoutes = fromatRoutes(Router.CONFIG.routes, true, CONFIG);
	Router.vueRoutes = objVueRoutes; //vue-routes
	Router.selfRoutes = { ...Router.selfRoutes || {},
		...objSelfRoutes
	}; //self-routes
	Router.$route = vueRouter; //vue-router$route
	rewriteUniFun(Router); //uniapp
	registerRouter(Router, vueRouter, CONFIG.vueRouterDev);
}
